@import "../helpers/all";

.search-form {
   position: relative;
   &__field {
      padding: 0 35px 0 20px;
      width: 100%;
      height: 50px;
      @include f('ms400', 14, 50);
      color: $color-1;
      border-bottom: 1px solid rgba(#989898, .3);
      @include placeholder-color(#989898);
      transition: .3s ease;
      &:focus {
         border-bottom-color: #989898; } }
   &__submit {
      display: none;
      &-btn {
         position: absolute;
         top: 0;
         right: 0;
         bottom: 0;
         width: 35px;
         display: flex;
         justify-content: flex-end;
         align-items: center;
         cursor: pointer; } }
   svg {
      width: 17px;
      height: 17px;
      opacity: .7;
      fill: $color-1; } }

.form-field {
   $parent: &;
   &__submit {
      width: 100%;
      box-shadow: none;
      letter-spacing: 1.28px;
      &:hover {
         background-color: #ffb401; } }
   &__input {
      padding: 0 0 0 20px;
      width: 100%;
      height: 50px;
      @include f('ms400', 16, 50);
      border-bottom: 1px solid rgba($white, .3);
      transition: .3s ease;
      background: transparent;
      color: $white;
      @include placeholder-color($white);
      &:focus {
         border-bottom-color: #989898; } } }
