    flex-direction: column;
    height: auto;
    padding: 40px 25px;
  }
}

.contact__block-img {
  position: absolute;
  top: 0;
  right: 0;
  pointer-events: none;
}

.contact__section-top {
  flex: 0 0 400px;
  color: #fff;
}

@media only screen and (max-width: 767px) {
  .contact__section-top {
    flex: 0 1 auto;
    width: 100%;
  }
}

.contact__section-top .title {
  margin-bottom: 25px;
  line-height: 53px;
}

@media only screen and (max-width: 767px) {
  .contact__section-top .title {
    line-height: 32px;
  }
}

.contact__section-top p {
  line-height: 31px;
  font-size: 20px;
  letter-spacing: 0.4px;
}

.contact__form {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: flex-end;
  flex: 0 1 570px;
  margin-top: 50px;
}

@media only screen and (max-width: 1199px) {
  .contact__form {
    flex-direction: column;
  }
}

@media only screen and (max-width: 767px) {
  .contact__form {
    flex: 0 1 auto;
    width: 100%;
    align-items: center;
  }
}

.contact__form-field:not(:last-child) {
  margin-right: 50px;
}

@media only screen and (max-width: 1199px) {
  .contact__form-field:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
  }
}

.contact__form-field:nth-child(1) {
  flex: 0 0 296px;
  margin-bottom: 6px;
}

@media only screen and (max-width: 1199px) {
  .contact__form-field:nth-child(1) {
    flex: 0 1 auto;
    width: 296px;
  }
}

@media only screen and (max-width: 767px) {
  .contact__form-field:nth-child(1) {
    width: 100%;
  }
}

.contact__form-field:nth-child(2) {
  flex: 0 0 225px;
}

@media only screen and (max-width: 1199px) {
  .contact__form-field:nth-child(2) {
    flex: 0 1 auto;
    width: 225px;
  }
}

.about {
  padding: 25px 0 78px;
}

.about__breadcrumbs {
  margin-bottom: 67px;
}

@media only screen and (max-width: 767px) {
  .about__breadcrumbs {
    margin-bottom: 40px;
  }
}

.about__info {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 70px;
}

@media only screen and (max-width: 1199px) {
  .about__info {
    flex-direction: column;
    margin-bottom: 40px;
  }
}

.about__info-block {
  display: flex;
  flex-wrap: wrap;
  margin: 88px -15px 0;
  flex: 0 0 460px;
}

@media only screen and (max-width: 1199px) {
  .about__info-block {
    flex: 0 1 auto;
    width: 100%;
    max-width: 490px;
    margin: 50px auto 0;
  }
}

.about__info-item {
  flex: 0 0 50%;
  max-width: 230px;
  padding: 0 15px;
}

@media only screen and (max-width: 767px) {
  .about__info-item {
    flex: 1 1 100%;
    max-width: none;
  }
}

@media only screen and (max-width: 767px) {
  .about__info-item:nth-child(1) ~ .about__info-item {
    margin-top: 30px;
  }
}

.about__info-item:nth-child(2) ~ .about__info-item {
  margin-top: 78px;
}

@media only screen and (max-width: 1199px) {
  .about__info-item:nth-child(2) ~ .about__info-item {
    margin-top: 40px;
  }
}

@media only screen and (max-width: 767px) {
  .about__info-item:nth-child(2) ~ .about__info-item {
    margin-top: 30px;
  }
}

.about__info-item-count {
  margin-bottom: 10px;
  color: #ffc90e;
  font-family: "Montserrat-Bold", Arial, sans-serif;
  font-weight: normal;
  font-size: 58px;
  line-height: 58px;
  letter-spacing: 1.16px;
}

@media only screen and (max-width: 767px) {
  .about__info-item-count {
    margin-bottom: 0;
  }
}

.about__info-item p {
  opacity: .8;
  letter-spacing: 0.28px;
  font-size: 14px;
}

.about__section-top {
  flex: 0 0 620px;
}

@media only screen and (max-width: 1199px) {
  .about__section-top {
    flex: 0 1 auto;
    width: 100%;
  }
}

.about__section-top .section-top__title {
  margin-bottom: 37px;
}

@media only screen and (max-width: 767px) {
  .about__section-top .section-top__title {
    margin-bottom: 25px;
  }
}
