$icons: (0:0);
$icons: map-merge($icons,('arr-r': (X: -149px, Y:-90px, W: 10px, H: 16px, TW: 177px, TH: 130px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('assembly-line': (X: -96px, Y:0px, W: 43px, H: 31px, TW: 177px, TH: 130px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('award': (X: 0px, Y:-47px, W: 34px, H: 40px, TW: 177px, TH: 130px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('customer': (X: -49px, Y:0px, W: 37px, H: 37px, TW: 177px, TH: 130px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('like': (X: -96px, Y:-41px, W: 34px, H: 35px, TW: 177px, TH: 130px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('plug': (X: 0px, Y:-97px, W: 33px, H: 33px, TW: 177px, TH: 130px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('smartphone': (X: -149px, Y:-44px, W: 22px, H: 36px, TW: 177px, TH: 130px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('support': (X: 0px, Y:0px, W: 39px, H: 37px, TW: 177px, TH: 130px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('trophy': (X: -149px, Y:0px, W: 28px, H: 34px, TW: 177px, TH: 130px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('truck': (X: -44px, Y:-47px, W: 39px, H: 28px, TW: 177px, TH: 130px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }


