.footer {
   padding: 52px 0 172px;
   background-color: #e4e7ea;
   @include md {
      padding: 52px 0 80px; }
   @include xs {
      padding: 50px 0; }
   &__container {
      display: flex;
      align-items: flex-start;
      @include sm {
         justify-content: space-between; }
      @include xs {
         flex-direction: column;
         align-items: flex-start; } }
   &__logo {
      align-self: flex-end;
      margin-right: 100px;
      @include sm {
         margin-right: 0; }
      @include xs {
         flex: 0 1 auto;
         margin: 0 auto 30px 0; } }
   &__nav {
      padding: 0 15px;
      @include sm {
         margin-left: auto;
         padding: 0; }
      @include xs {
         margin-left: 0; }
      ul {
         column-count: 2;
         column-width: 200px;
         column-gap: 30px;
         @include sm {
            column-width: 160px; }
         @include xs {
            column-width: auto;
            column-gap: 0;
            column-count: unset; }
         li {
            &:not(:last-child) {
               margin-bottom: 5px;
               @include xs {
                  margin-bottom: 10px; } }
            &:nth-child(1),
            &:nth-child(5) {
               margin-bottom: 20px;
               @include xs {
                  margin-bottom: 10px; } }
            a {
               color: #4e5154;
               font-size: 14px;
               letter-spacing: 0.28px;
               @include trs;
               &:hover {
                  color: $color-3; } } } } } }
